.Quiz {
    max-width: 600px;
    margin: 0 auto;
    padding: 20px;
  }
  
  .Question-section {
    margin-bottom: 20px;  
  }
  
  .Question-count {
    font-size: 14px;
    font-weight: bold;
    margin-bottom: 5px;
  }
  
  .Question-text {
    font-size: 20px;
    font-weight: bold;
  }
  
  .Answer-section {
    display: flex;
    flex-direction: column;
    gap: 10px;
  }
  
  button {
    width: 100%;
    padding: 10px;
    border: 1px solid #ddd;
    border-radius: 5px;
    background-color: #faed62;
    font-size: 16px;
  } 
  
  button.correct {
    color:  rgb(7, 30, 160);
    font-size: 20px;
    font-weight: bold;
  }
  
  button.incorrect {
    color: rgb(7, 30, 160); 
    font-size: 20px;
    font-weight: bold;
  }
  
  .Score-section {
    text-align: center;
    font-size: 24px;
    font-weight: bold;
    margin-top: 20px; 
  }